import myButton from '../../../components/my-components/RoundButton';
export default {
    props: {
        rights: {
            type: Object,
            default: () => ({
                status: '退款退货',
                addtion: [],
                subStatus: 0 //订单子状态，1：维权
            })
        }
    },
    components: {
        myButton
    },
    computed: {
        canSendGood() { //判断可否发货
            if (this.rights.groups_success == 1 || this.rights.groups_success === undefined) {
                return !!this.rights.send_able ? "primary" : "disable"
            } else {
                return "disable";
            }
        },
    },
    methods: {
        clickBill() {
            this.$emit('click', {
                type: 'rights',
                detail: {
                    val: this.rights
                }
            })
        },
        clickButton(val) {
            this.$emit('click', {
                type: 'button',
                detail: {
                    val,
                    righting: false
                }
            })
        }
    },
}
